body {
  font-family: "Gill Sans", sans-serif;
  margin: 0px;
  background-color: #c9e4e7;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
}
.header {
  font-size: 1.6em;
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
  color: #1e1014;
}
.sub-header {
  font-size: 1.2em;
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: normal;
  font-style: italic;
}

@media only screen and (max-width: 480px) {
  .header {
    font-size: 1em;
    margin-block-start: 0em;
    margin-block-end: 0em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: bold;
  }
  .sub-header {
    font-size: 1em;
    margin-block-start: 0em;
    margin-block-end: 0em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: normal;
    font-style: italic;
  }
}
.header-w {
  position: fixed;
  top: 0px;
  width: 100%;
  background-color: #b4a0e5;
  display: flex;
  flex-direction: row;
}
.header-left {
  width: 36px;
  border-right: 8px solid #c9e4e7;
}
.header-left-inner {
  height: 100%;
  border-right: 16px solid rebeccapurple;
}
.header-right {
  padding: 8px;
}
.content-container {
  margin-top: 68px;
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.content {
  font-size: 1.2em;
  padding: 16px;
  line-height: 1.6em;
  max-width: 1024px;
}
.footer {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  padding: 4px;
  background-color: #b4a0e5;
  color: #fff;
  position: fixed;
  bottom: 0px;
  color: #1e1014;
}
